import { Component, EventEmitter, Output, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {
  BulkOperationsService,
  OperationType
} from '@c8y/ngx-components/operations/bulk-operations-service';
import { BulkOperationModalsService } from '../modals/bulk-operation-modals.service';

@Component({
  selector: 'c8y-bulk-operation-type-list',
  templateUrl: './bulk-operation-type-list.component.html'
})
export class BulkOperationTypeListComponent implements OnInit {
  @Output() onCustomType: EventEmitter<OperationType> = new EventEmitter();
  bulkList: OperationType[] = [];

  constructor(
    private router: Router,
    private bulkOperationsService: BulkOperationsService,
    private bulkOperationModalsService: BulkOperationModalsService
  ) {}

  ngOnInit() {
    this.bulkList = this.bulkOperationsService.getBulkTypes();
  }

  handleClick(item: OperationType) {
    if (item.path) {
      this.bulkOperationModalsService.hideNewBulkOperationModal();
      this.router.navigateByUrl(item.path);
    } else if (item.component) {
      this.onCustomType.emit(item);
    }
  }
}
