import { BulkOperationWrapperComponent } from './bulk-operation-wrapper.component';
import { BulkOperationTypeListComponent } from './type-list/bulk-operation-type-list.component';
import { BulkOperationModalsService } from './modals/bulk-operation-modals.service';
import { BulkOperationsModalComponent } from './modals/bulk-operations-modal.component';
